:root{
    --color-accent:#ff3400;
    --color-body:#918ca4;   
    --color-headings:#1b0760;
    --color-primary:#2584ff;
    --color-secondary:#00d9ff;
    --color-border:#ccc;
    --border-raduis:30px;
    --color-body-darker:#5c5577;
}
*,*::after,*::before{
    box-sizing: border-box;
}
/*Typography*/
html{
    font-size: 10px;
}
body{
    color: var(--color-body);
    font-family: inter,Arial, Helvetica, sans-serif;
    font-size: 2.4rem;
    line-height: 1.5;
}
h1,h2,h3{
    color: var(--color-headings);
    margin-bottom: 1.5rem ;
    line-height: 1.1;

}
h1{
    font-size: 7rem;
}
h2{
    font-size: 4rem;
}
h3{
    font-size: 3rem;
}
p{
    margin-top: 0;
}
@media screen and (min-width:1024px) {
    body{
        font-size: 1.8rem;
    }
    h1{
        font-size: 8rem;
    }
    h2{
        font-size: 4rem;
    } 
    h3{
    font-size: 2.4rem;
    }   
}
/*Link*/
a{
    text-decoration: none;
}
.link-arrow{
    color: var(--color-accent);
    font-size: 2rem;
    font-weight: bold;
    text-transform: uppercase;

}
.link-arrow::after{
     content: '-->';
     margin-left: 5px;
     transition: margin .12s;
}
.link-arrow:hover:after{
    margin-left: 10px;
}
@media screen and (min-width:1024px) {
.link-arrow{
    font-size: 1.5rem;
}
}
/*badge*/
.badge{
    border-radius: 20px;
    color: #fff;
    font-size: 2rem;
    font-weight: 600;
    font-weight: bold;
    padding: .5rem 2rem;
    white-space: nowrap;
}
.badge--primary{
    background: var(--color-primary);
}
.badge--secondary{
    background: var(--color-secondary);
}
.badge--small{
    font-size: 1.6rem;
    padding: .5rem 1.5rem;
}
@media screen and (min-width:1024px) {
    .badge{
    font-size: 1.5rem;
    }
    .badge--small{
        font-size: 1.2rem; 
    }
}
/*list*/
.list{
    list-style: none;
    padding-left: 0;
   
}
.list--inline .list__item{
    display: inline-block;
    margin-left: 2rem;
}
.list--tick{
    list-style-image: url(../images/tick.svg);
    padding-left: 3rem;
    color: var(--color-headings);
}
.list--tick .list__item{
    padding-left: .5rem;
    margin-bottom: 1rem;
}
@media screen and (min-width:1024px) {
    .list--tick .list__item{
        padding-left: 0;
  } 
}
/*icon*/
.icon{
    width: 40px;
    height: 40px;
}
.icon--small{
    width: 30px;
    height: 30px;

}
.icon--primary{
    fill: var(--color-primary);
}
.icon--white{
    fill: #fff;
}
.icon-container{
    align-items: center;
    background: #f3f9fa;
    border-radius: 100%;
    display: inline-flex;
    height: 64px;
    justify-content: center;
    width: 64px;
}
.icon-container--accent{
    background: var(--color-accent);
}
/*Button*/
.btn{
    border-radius: 40px;
    border: 0;
    cursor: pointer;
    font-size: 1.8rem;
    font-weight: 600;
    padding: 1.5rem 4vw;
    text-transform: uppercase;
    white-space: nowrap;
    text-align: center;
    margin: 1rem 0;
}
.btn .icon{
    width: 2rem;
    height: 2rem;
    margin-right: 1rem;
    vertical-align: middle;
}
.btn--primary{
    background-color: var(--color-primary);
    color: #fff;
}
.btn--primary:hover{
    background-color: #3a8ffd;
}
.btn--secondary{
    background-color: var(--color-secondary);
    color: #fff;
}
.btn--secondary:hover{
    background-color: #00c8eb;
}
.btn--accent{
    background-color: var(--color-accent);
    color: #fff;
    }
.btn--accent:hover{
    background-color: #fd4516;
}
.btn--block{
    display: inline-block;
    width: 100%;
}
.btn--outline{
    color:var(--color-headings);
    background: #fff;
    border: 2px solid var(--color-headings);
}
.btn--outline:hover{
    background: var(--color-headings);
    color: #fff;
}
.btn-streched{
    padding-right: 6rem;
    padding-left: 6rem;
}
@media screen and (min-width:1024px) {
    .btn{
        font-size: 1.5rem;
    }
    
}
/*Inputs*/
.input{
    border-radius: var(--border-raduis); 
    border: 1px solid var(--color-border);
    color: var(--color-headings);
    font-size: 2rem;
    outline: 0;
    padding: 1.5rem 2.5rem;
}
::placeholder{
    color: #cdcbd7;
}
.input-group{
    border-radius: var(--border-raduis);
    border: 1px solid var(--color-border);
    display: flex;
}
.input-group .input{
    border: 0;
    flex-grow: 1;
    padding: 1.5rem 2rem;
    width: 0;
}
.input-group .btn{
    margin: 4px;
}
@media screen and (min-width:1024px){
    .input{
        font-size: 1.5rem;
    }    
}
/*Cards*/
.card{
    border-radius: 7px;
    box-shadow: 0 0 10px #f3f3f3; 
    overflow: hidden;  
}
.card__header,.card__body{
    padding: 2rem 3rem;
}
.card--primary .card__header{
    background-color: var(--color-primary);
    color: #fff;
}
.card--secondary .card__header{
    background-color: var(--color-secondary);
    color: #fff;
}
.description{
    display: block;
    font-size: 2rem;
    font-weight: 300;
    letter-spacing: 1px;
}
/*plan*/
.plan{
    transition: transform .2s ease-out;
}

.plan__name{
    color: #fff;
    margin: 0;
    font-weight: 400;
    font-size: 2.4rem;
}
.plan__price{
    font-size: 6rem;
}
.plan__billing-cycle{
    font-size: 2.4rem;
    font-weight: 300;
    opacity: .8;
    margin-right: 1rem;
}
.card--secondary .badge--secondary{
    background-color: var(--color-primary);
}
.card--primary .badge--primary{
    background-color: var(--color-secondary);
}
.plan .list__item{
    margin-bottom: 2rem;
}
.plan--popular{
    transform: scale(1.1);
}
.plan--popular .card__header{
    position: relative;
}
.plan--popular .card__header::before{
    content: url(../images/popular.svg);
    width: 40px;
    display: inline-block;
    position: absolute;
    top: -6px;
    right: 5%;
}
.plan:hover{
    transform: scale(1.05);
}
.plan--popular:hover{
    transform: scale(1.15);
}
.media{
    display: flex;
}
.media__title{
    margin-top: 0;
}
.media__body{
    margin: 0 2rem;
}
.media__image{
    margin-top: 2rem;
}
/*Quote*/
.quote{
    line-height: 1.3;
    font-size: 3rem;
    font-style: italic;
    color:var(--color-body-darker) ;
}
.quote__text::before{
    content: open-quote;
}
.quote__text::after{
    content: close-quote;
}
.quote__author{
    font-size: 3rem;
    font-weight: 500;
    margin-bottom: 0;
    font-style: normal;
}
.quote__organization{
color:var(--color-headings);
opacity: .4;
font-size: 2rem;
font-style: normal;
}
.quote__line{
    position: relative;
    bottom: 15px;
}
/*grid*/
.grid{
    display: grid;
}

@media screen and (min-width: 768px){
    .grid--1x2{
        grid-template-columns: 1fr 1fr;
    }  
}
@media screen and (min-width: 1024px){
    .grid--1x3{
        grid-template-columns: 1fr 1fr 1fr;
    }  
}
/*testimonil*/
.testimonial{
    padding: 3rem;
}
.testimonial__image{
    position: relative;
}
.testimonial__image > .icon-container{
    position: absolute;
    top: 3rem;
    right: -32px;
}
.testimonial__image >img{
    width: 100%;
}
@media screen and (min-width:768px) {
    .testimonial .quote,
    .testimonial .quote__author{
        font-size: 2.4rem;
         }
         .testimonial .quote{
            margin-left: 6rem;
         }
 }
 /*Callout*/
 .callout{
    padding: 4rem;
    border-radius: 5px;
 }
 .callout--primary{
    background-color: var(--color-primary);
    color: #fff;
 }
 .callout__heading{
    color: #fff;
    margin-top: 0;
    font-size: 3rem;
 }
 .callout .btn{
    justify-self: center;
    align-self: center;
 }
 .callout__content{
    text-align: center;
 }
 @media screen and (min-width:768px) {
    .callout .grid--1x2{
        grid-template-columns: 1fr auto;
    }
    .callout__content{
        text-align: left;
    }
    .callout .btn{
        justify-self: start;
        margin: 0 2rem;
    }
 }
 /*collapsible*/
 .collapsible__header{
    display: flex;
    justify-content: space-between;
 }
 .collapsible__heading{
    margin-top: 0;
    font-size: 3rem;
 }
 .collapsible__chevron{
    transform: rotate(-90deg);
    transition: transform .3s;

 }
 .collapsible__content{
    opacity: 0;
    max-height: 0;
    overflow: hidden;
    transition: all 0.5s ;
 }
 .collapsible--expanded .collapsible__chevron{
transform: rotate(0);
 }
 .collapsible--expanded .collapsible__content{
max-height: 100vh; 
opacity: 1;}
/*Blocks*/
.block{
    --padding-vertical:3rem;
    padding: var(--padding-vertical) 2rem;
   
}
.block__heading{
margin-top: 0;
}
.block--dark{
    background: #000;
    color: #7b858b;
}
.block--dark h1,.block--dark h2,.block--dark h3{
    color: #fff;
}
.block--skewed-right{
    padding-bottom: calc(var(--padding-vertical) + 4rem);
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 90%);
}
.block--skewed-left{
    padding-bottom: calc(var(--padding-vertical) + 4rem);
    clip-path: polygon(0 0, 100% 0, 100% 90%, 0 100%);
}
.block__header{
    text-align: center;
}
.container{
    max-width: 1140px;
    margin: 0 auto;
}
/*Navigation*/
.nav{
    background: #000;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0 1rem;
    align-items: center;
}
.nav__list{
    width: 100%;
    margin: 0;
}
.nav__item{
    padding: .5rem 2rem;
    border-bottom: 1px solid #222;
}
.nav__item>a{
color: #d2d0db;
transition: color .3s;
}
.nav__item>a:hover{
    color: #fff;
}
.nav__toggler{
    opacity: .5;
    transition: box-shadow .15s;
    cursor: pointer;
}
.nav.collapsible--expanded .nav__toggler{
    opacity: 1;
    box-shadow: 0 0 0 3px #667;
    border-radius: 5px;
}
@media screen and (min-width:768px) {
    .nav__toggler{
        display: none;
    }
    .nav__list{
        width: auto;
        display: flex;
        font-size: 1.6rem;
        max-height: 100%;
        opacity: 1;
       
    }
    .nav__item{
        border: 0;
    }
    
}
/*Hero*/
.hero__tagline{
    font-size: 2rem;
    color: #b9c3cf;
    letter-spacing: 1px;
    margin: 2rem 0 5rem;
}
.hero__image{
    width: 100%;
}
@media screen and (min-width:768px) {
    .hero{
        padding-top: 0;
    }
    .hero__content{
        text-align: left;
        align-self: center;
    }
  
    
}
.block-domain .input-group{
    box-shadow: 0 0 30px 20px #e6ebee;
    border: 0;
    margin: 4rem auto;
    max-width: 700px;
}
.block-domain__prices{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: repeat(2,6rem);
    font-size: 2rem;
    font-weight: 600;
    justify-items: center;
    max-width: 800px;
    margin: 0 auto;
}
@media screen and (min-width:768px) {
    .block-domain__prices{
        grid-template-columns: repeat(auto-fit,minmax(10rem,1fr));
    }
}

.block-plans .grid{
    gap: 8rem 4rem;
}
.block-plans .card{
    max-width: 400px;
    margin: 0 auto;
}
/*feature*/
.feature{
    gap:4rem;
    margin: 1rem 0;
}
.feature:first-of-type{
    margin-top: 6rem;
}
.feature__heading{
    margin: 1rem 0 ;
}
.feature__image{
width: 100%;
}
@media screen and (min-width:768px) {
    .feature:nth-of-type(even) .feature__content{
        order: 2;
    }
}
/*Showcase Block*/
.block-showcase__image{
    width: 100%;
}
@media screen and (min-width:768px){
    .block-showcase__image {
        width:auto; 
        justify-self: end;
        max-width: 700px;
        overflow:visible;
    }
    .block-showcase .grid {
        grid-template-columns: 50% 50%;
    }
}
/*footer*/
.footer{
    background: #232323;
}
.footer a{
color: #777;
transition: color .3s;
}
.footer a:hover{
    color: #fff;
}
.footer__section{
    padding: 2rem;
    border-bottom: 1px solid #393939;
   
}
.footer__section .list{
    margin: 0;
   
}
.footer__heading{
    text-transform: uppercase;
    font-weight: 600;
   
}
.footer__brand{
    margin-top: 5rem;
    text-align: center;
}
.footer_brand img{
    width: 100%;
    max-width: 230px;
}
.footer__copyright{
    font-size: 2.1rem;
    color: #fff;
    opacity: .3;
}
@media screen and (min-width:768px) {
    .footer__sections{
    grid-template-columns: repeat(auto-fit,minmax(10rem,1fr));
    }
    .footer .collapsible__chevron{
        display: none;
    }
    .footer .collapsible__content{
        opacity: 1;
        max-height: 100%;
    }
    .footer__brand{
        order: -1;
        margin-top: 1rem;
    }
    .footer__copyright{
font-size: 1.5rem;
    }
    .footer__section{
        border: 0;
    }

}